
* {
  margin: 0;
  padding: 0;
}
div {
  box-sizing: border-box;
}
.main-content .center-content {
  position: fixed;
  z-index: 1011;
  width: 7.2rem;
  height: 6.88rem;
  top: 50%;
  left: 50%;
  margin-top: -3.44rem;
  margin-left: -3.6rem;
  background-color: #ffffff;
  border-radius: 0.26667rem;
}
.main-content .center-content .question-wapper {
    padding: 0.61333rem 0.64rem;
}
.main-content .center-content .question-wapper .question-title {
      color: #1a1a1a;
      font-size: 0.50667rem;
      font-weight: 700;
      text-align: center;
}
.main-content .center-content .question-wapper .question-list {
      *height: 1%;
      margin-top: 0.61333rem;
}
.main-content .center-content .question-wapper .question-list:after {
        clear: both;
        content: '.';
        display: block;
        height: 0;
        line-height: 0;
        overflow: hidden;
}
.main-content .center-content .question-wapper .question-list .question-item {
        width: 1.17333rem;
        height: 1.17333rem;
        font-size: 0.50667rem;
        line-height: 1.17333rem;
        color: #3366cc;
        text-align: center;
        border: 1px solid #c0c6cd;
        float: left;
        margin-left: 0.4rem;
        border-radius: 0.05333rem;
}
.main-content .center-content .question-wapper .question-list .question-item:nth-child(4n+1) {
          margin-left: 0;
}
.main-content .center-content .question-wapper .question-list .question-item:nth-child(n+5) {
          margin-top: 0.53333rem;
}
.main-content .body-cover {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1010;
}



